<template>
  <div class="subteams">
    <div class="btn-wrapper">
        <button class="btn" @click="newTeam = true">Create New Subteam</button>
    </div>
    <NewSubTeam v-if="newTeam" @close="newTeam = false"/>
    <SubTeam v-for="(team, index) in subteams" :key="keys[index]" :data="team" />
  </div>
</template>

<script>
import { subteams } from "../firebase"
import { getDocs } from "firebase/firestore"
import SubTeam from '@/components/SubTeam.vue'
import NewSubTeam from '@/components/NewSubTeam.vue'

export default {
    name: "SubteamsView",
    components: {
        SubTeam,
        NewSubTeam
    },
    data() {
        return {
            subteams: [],
            keys: [],
            newTeam: false,
        }
    },
    async created(){
        const docs = await getDocs(subteams)
        
        docs.forEach(doc => {
            this.subteams.push(doc.data())
            this.keys.push(doc.id)
        })
    }
}
</script>

<style lang="scss" scoped>
.subteams
{
    box-sizing: border-box;
    padding: 2em;

    display: flex;
    flex-flow: column nowrap;
    gap: 1em;
    align-items: center;

    flex: 1 1 auto;
    min-height: 0;

    overflow-y: scroll;

    .btn-wrapper
    {
        width: 50%;
        text-align: right;
    }
}
</style>