<template>
  <div class="home">
    <div class="outer-wrapper about">
      <label for="">About:</label>
      <textarea name="" id="" v-model="info.about"></textarea>
    </div>

    <div class="outer-wrapper mission">
      <label for="">Mission:</label>
      <textarea name="" id="" v-model="info.mission"></textarea>
    </div>

    <div class="outer-wrapper intro-video">
      <label for="">Intro Video:</label>
      <input type="text" v-model="info.introVideoURL">
      <embed :src="info.introVideoURL" type="">
    </div>

    <div class="outer-wrapper discord">
      <label for="">Discord Link</label>
      <input type="text" v-model="info.discord">
    </div>

    <div class="outer-wrapper email">
      <label for="">Email</label>
      <input type="text" v-model="info.email">
    </div>

    <div class="outer-wrapper instagram">
      <label for="">Instagram</label>
      <input type="text" v-model="info.instagram">
    </div>

    <div class="outer-wrapper donate">
      <label for="">Donate Link</label>
      <input type="text" v-model="info.donateLink">
    </div>

    <button class="btn save" v-if="newChange" @click="uploadChanges">Save Changes</button>
  </div>
</template>

<script>
import {info} from "../firebase"
import { getDoc, setDoc } from "firebase/firestore"

export default {
  name: 'HomeView',
  data() {
    return {
      info: {},
      newChange: false
    }
  },
  async created(){
    await getDoc(info).then((doc) => {
      if(!doc.exists())
      {
        console.log("Website info deleted")
        return
      }

      this.info = doc.data()
    })
  },
  watch: {
    info: {
      handler(newInfo, oldInfo)
      {
        if(Object.keys(oldInfo).length > 0)
        {
          console.log("Changes Made!")
          this.newChange = true;
        }

      }, 
    deep: true
    }
  },
  methods: {
    async uploadChanges()
    {
      await setDoc(info, this.info).then(() => {console.log("Saved")})
    }
  }
}
</script>

