<template>
  <div class="subteam" ref="instance">
    <div class="image-wrapper">
        <input type="file" ref="files" accept=".svg" @change="showImage">
        <img :src="imageURL" alt="">
    </div>
    <input type="text" v-model="title">

    <button class="btn save" v-if="newChange" @click="uploadChanges">
        <svg clip-rule="evenodd" fill-rule="evenodd" stroke-linejoin="round" stroke-miterlimit="2" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path d="m2.25 12.321 7.27 6.491c.143.127.321.19.499.19.206 0 .41-.084.559-.249l11.23-12.501c.129-.143.192-.321.192-.5 0-.419-.338-.75-.749-.75-.206 0-.411.084-.559.249l-10.731 11.945-6.711-5.994c-.144-.127-.322-.19-.5-.19-.417 0-.75.336-.75.749 0 .206.084.412.25.56" fill-rule="nonzero"/></svg>
    </button>

    <button class="btn delete" @click="deleteInstance">
        <svg clip-rule="evenodd" fill-rule="evenodd" stroke-linejoin="round" stroke-miterlimit="2" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path d="m20.015 6.506h-16v14.423c0 .591.448 1.071 1 1.071h14c.552 0 1-.48 1-1.071 0-3.905 0-14.423 0-14.423zm-5.75 2.494c.414 0 .75.336.75.75v8.5c0 .414-.336.75-.75.75s-.75-.336-.75-.75v-8.5c0-.414.336-.75.75-.75zm-4.5 0c.414 0 .75.336.75.75v8.5c0 .414-.336.75-.75.75s-.75-.336-.75-.75v-8.5c0-.414.336-.75.75-.75zm-.75-5v-1c0-.535.474-1 1-1h4c.526 0 1 .465 1 1v1h5.254c.412 0 .746.335.746.747s-.334.747-.746.747h-16.507c-.413 0-.747-.335-.747-.747s.334-.747.747-.747zm4.5 0v-.5h-3v.5z" fill-rule="nonzero"/></svg>
    </button>
  </div>
</template>

<script>
import { subteams } from "../firebase"
import { setDoc, doc, deleteDoc } from "firebase/firestore"
import { getStorage, ref, getDownloadURL, uploadBytes } from "firebase/storage"

export default {
    name: "SubTeam",
    props: {
        data: Object
    },
    data() {
        return {
            title: "",
            imageURL: "",
            files: [],
            newChange: false,
            newURL: false
        }
    },
    watch: {
        title: function(newTitle, oldTitle)
        {
            if(oldTitle.length > 0)
                this.newChange = true
        }
    },
    created()
    {
        this.title = this.$props.data.title
        if (this.$props.data.image.length > 0)
            getDownloadURL(ref(getStorage(), this.$props.data.image)).then((url) => this.imageURL = url)
    },
    methods: {
        showImage()
        {
            const [file] = this.$refs.files.files
            if (file)
            {
                this.imageURL = URL.createObjectURL(file)
                this.newChange = true
                this.newURL = true
            }
        },
        async uploadChanges()
        {
            let title = this.imageURL
            if (this.newURL)
            {
                title = `${this.title}.svg`
                uploadBytes(ref(getStorage(), title), this.$refs.files.files[0]).then(() => console.log("Image Uploaded"))
            }

            await setDoc(doc(subteams, this.$.vnode.key), {
                title: this.title,
                image: title
            }).then(() => {
                this.newURL = false;
                this.newChange = false;
            })
        },
        async deleteInstance()
        {
            if(confirm(`Are you sure you want to delete ${this.$props.title}`))
            {
                await deleteDoc(doc(subteams, this.$.vnode.key)).then(() => {
                    this.$refs.instance.remove()
                })
            }
        }
    }
}
</script>

<style lang="scss" scoped>
.subteam
{
    display: flex;
    flex-flow: row wrap;
    border: 1px solid black;
    padding: 0.5em;
    width: 50%;
    gap: 1em;
    box-sizing: border-box;

    .image-wrapper {
        width: 2em;
        height: 2em;
        position: relative;
        &:hover{cursor: pointer;}
    }

    input{
        border: none;
        margin-right: auto;
    }

    input[type=file]
    {
        opacity: 0;
        width: 100%;
        height: 100%;
        position: relative;
        z-index: 2;
        &:hover{
            cursor: pointer;
        }
    }
    img{
        position: absolute;
        z-index: 1;
        width: 2em;
        height: 2em;
        top: 0;
        left: 0;
    }

    .btn{
        svg {
            fill: #fff;
            height: 1.5em;
        }

        &.delete{
            background-color: transparent;

            svg{fill: red;}
        }
    }
}
</style>