<template>
  <div class="login">
    <input type="text" ref="uname" placeholder="Username">
    <input type="password" ref="pword" placeholder="Password">
    <button @click="login">Login</button>
  </div>
</template>

<script>
export default {
    name: "LogIn",
    data() {
        return {
            password: "YmFzaWNwYXNzd29yZA==",
            username: "crs"
        }
    },
    methods: 
    {
        login: function()
        {
            let pword = this.$refs.pword.value
            let uname = this.$refs.uname.value

            if((uname == this.username) && (btoa(pword) == this.password))
            {
                sessionStorage.setItem("loggedIn", true)
                this.$router.push({name: "home"})
            }
        }
    }
}
</script>

