<template>
  <div class="new-member">
    <div class="image-wrap">
        <input type="file" ref="files" accept=".jpeg,.jpg,.png" @change="previewImage">
        <img :src="imageURL" alt="">
    </div>
    <div class="info">
        <span class="info-wrap">
            <span>First Name:</span>
            <input type="text" v-model="info.first_name">
        </span>
        <span class="info-wrap">
            <span>Last Name:</span>
            <input type="text" v-model="info.last_name">
        </span>
        <span class="info-wrap">
            <span>Email:</span>
            <input type="email" v-model="info.email">
        </span>
        <span class="info-wrap">
            <span>Role:</span>
            <input type="text" v-model="info.role">
        </span>
        <span class="info-wrap">
            <span>Linkedin:</span>
            <input type="text" v-model="info.linkedinURL">
        </span>
        <span class="info-wrap">
            <span>Is Leadership?</span>
            <input type="checkbox" v-model="info.isLeadership">
        </span>
        <span class="info-wrap">
            <span>Sub Team:</span>
            <select name="" id="" v-model="info.subteam">
                <option value=""></option>
                <option v-for="(subteam, index) in subteams" :key="index" :value="subteam">{{subteam}}</option>
            </select>
        </span>
    </div>
    <div class="btns">
        <button class="btn" @click="create">
            Create
        </button>
        <button class="btn delete" @click="$emit('close')">
            Cancel
        </button>
    </div>
  </div>
</template>

<script>
import { members } from "../firebase"
import { addDoc } from "firebase/firestore"
import { getStorage, ref, uploadBytes } from "firebase/storage"

export default {
    name: "NewMember",
    props: {
        subteams: Array
    },
    data() {
        return {
            info: {},
            imageURL: "",
            change: false,
            newImage: false
        }
    },
    methods: {
        previewImage: function()
        {  
            const file = this.$refs.files.files[0]
            if(file)
            {
                this.imageURL = URL.createObjectURL(file)
            }
        },
        create: async function()
        {
            let imageURL = ""
            if(this.imageURL.length > 0)
            {
                let type = this.$refs.files.files[0].name.split(".").pop()
                imageURL = `${this.info.first_name}${this.info.last_name}.${type}`
                uploadBytes(ref(getStorage(), imageURL), this.$refs.files.files[0])
            }
            this.info.image = imageURL

            await addDoc(members, this.info ).then(() => {
                this.$emit("close")
            })
        },
    }
}
</script>

<style lang="scss" scoped>
.new-member
{
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    box-sizing: border-box;
    padding: 2em;
    gap: 0.5em;

    .image-wrap{
        width: 7em;
        height: 7em;
        border-radius: 10em;
        overflow: hidden;
        position: relative;
        background-color: gray;

        img
        {
            position: absolute;
            width: 100%;
            background-size: auto;
            background-position: center;
            z-index: 1;
            left: 0;
            top: 0;
        }

        input[type=file]
        {
            position: relative;
            width: 100%;
            height: 100%;
            z-index: 2;
            opacity: 0;
            &:hover{
                cursor: pointer;
            }
        }
    }

    .info-wrap
    {
        display: flex;
        flex-flow: column nowrap;
        justify-content: center;
        align-items: center;
    }

    .btn.delete
    {
        background-color: red;
    }
}
</style>