<template>
  <div class="new-wrapper">
    <img :src="sampleImg" alt="">
    <input type="file" accept=".svg" ref="files" @change="showImage">
    <input type="text" name="" id="" v-model="name" placeholder="Subteam Name">
    
    <button class="btn save" @click="upload">
        <svg clip-rule="evenodd" fill-rule="evenodd" stroke-linejoin="round" stroke-miterlimit="2" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path d="m2.25 12.321 7.27 6.491c.143.127.321.19.499.19.206 0 .41-.084.559-.249l11.23-12.501c.129-.143.192-.321.192-.5 0-.419-.338-.75-.749-.75-.206 0-.411.084-.559.249l-10.731 11.945-6.711-5.994c-.144-.127-.322-.19-.5-.19-.417 0-.75.336-.75.749 0 .206.084.412.25.56" fill-rule="nonzero"/></svg>
    </button>

    <button class="btn delete" @click="$emit('close')">
        <svg clip-rule="evenodd" fill-rule="evenodd" stroke-linejoin="round" stroke-miterlimit="2" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path d="m20.015 6.506h-16v14.423c0 .591.448 1.071 1 1.071h14c.552 0 1-.48 1-1.071 0-3.905 0-14.423 0-14.423zm-5.75 2.494c.414 0 .75.336.75.75v8.5c0 .414-.336.75-.75.75s-.75-.336-.75-.75v-8.5c0-.414.336-.75.75-.75zm-4.5 0c.414 0 .75.336.75.75v8.5c0 .414-.336.75-.75.75s-.75-.336-.75-.75v-8.5c0-.414.336-.75.75-.75zm-.75-5v-1c0-.535.474-1 1-1h4c.526 0 1 .465 1 1v1h5.254c.412 0 .746.335.746.747s-.334.747-.746.747h-16.507c-.413 0-.747-.335-.747-.747s.334-.747.747-.747zm4.5 0v-.5h-3v.5z" fill-rule="nonzero"/></svg>
    </button>
  </div>
</template>

<script>
import { subteams } from "../firebase"
import { addDoc } from "firebase/firestore"
import { getStorage, ref, uploadBytes } from "firebase/storage"


export default {
    name: "NewSubTeam",
    data(){
        return {
            name: "",
            sampleImg: ""
        }
    },
    methods: {
        showImage: function()
        {
            const file = this.$refs.files.files[0]
            if(file)
                this.sampleImg = URL.createObjectURL(file)
        },
        upload: async function()
        {
            if(this.name.length == 0)
            {
                alert("Must give the subteam a name before continuing")
                return
            }
            let imageURL = ""

            if (this.sampleImg.length != 0)
            {
                imageURL = `${this.title}.svg`

                uploadBytes(ref(getStorage(), imageURL), this.$refs.files.files[0])
            }

            
            await addDoc(subteams, {
                title: this.name,
                image: imageURL
            }).then(() => {
                this.$emit("close")
            })
        }
    }
}
</script>

<style lang="scss" scoped>
.new-wrapper
{
    display: flex;
    flex-flow: row wrap;
    border: 1px solid black;
    padding: 0.5em;
    width: 50%;
    gap: 1em;
    box-sizing: border-box;

    .btn.delete {background-color: red;}

    svg{
        fill: #fff;
        height: 1.5em;
    }

    input[type=file]
    {
        height: 2em;
    }

    img
    {
        width: 2em;
        height: 2em;
    }
}
</style>