<template>
  <div class="members">
    <div class="add-wrapper">
        <button class="btn" @click="newMember = true">
            Add Member
        </button>
    </div>
    <NewMember v-if="newMember" :subteams="subteams" @close="newMember=false"/>
    <MemberTile v-for="(member, index) in membersList" :key="index" :member="member" :memberID="keys[index]" :subteams="subteams" />
  </div>
</template>

<script>
import { subteams, members } from "../firebase"
import { getDocs } from "firebase/firestore"
import MemberTile from '@/components/MemberTile.vue'
import NewMember from '@/components/NewMember.vue'

export default {
    name: "MembersView",
    components: {
        MemberTile,
        NewMember
    },
    data() {
        return {
            subteams: [],
            keys: [],
            membersList: [],
            newMember: false
        }
    },
    async created()
    {
        const teams = await getDocs(subteams)
        teams.forEach(team => {
            this.subteams.push(team.data().title)
        })

        const mems = await getDocs(members)
        mems.forEach(member => {
            this.membersList.push(member.data())
            this.keys.push(member.id)
        })
    }
}
</script>

<style lang="scss" scoped>
.members
{
 flex: 0 1 auto;
 min-height: 0;
 overflow-y: scroll;

 box-sizing: border-box;
 padding: 2em;
 display: flex;
 flex-flow: column nowrap;
 align-items: center;
 gap: 1em;

 .add-wrapper
 {
    text-align: right;
    width: 100%;
 }
}
</style>