<template>
  <div class="member-tile">
    <div class="image-wrap">
        <input type="file" ref="files" accept=".jpeg,.jpg,.png" @change="previewImage">
        <img :src="imageURL" alt="">
    </div>
    <div class="info">
        <span class="info-wrap">
            <span>First Name:</span>
            <input type="text" v-model="info.first_name">
        </span>
        <span class="info-wrap">
            <span>Last Name:</span>
            <input type="text" v-model="info.last_name">
        </span>
        <span class="info-wrap">
            <span>Email:</span>
            <input type="email" v-model="info.email">
        </span>
        <span class="info-wrap">
            <span>Role:</span>
            <input type="text" v-model="info.role">
        </span>
        <span class="info-wrap">
            <span>Linkedin:</span>
            <input type="text" v-model="info.linkedinURL">
        </span>
        <span class="info-wrap">
            <span>Is Leadership?</span>
            <input type="checkbox" v-model="info.isLeadership">
        </span>
        <span class="info-wrap">
            <span>Sub Team:</span>
            <select name="" id="" v-model="info.subteam">
                <option value=""></option>
                <option v-for="(subteam, index) in subteams" :key="index" :value="subteam">{{subteam}}</option>
            </select>
        </span>
    </div>
    <div class="btns">
        <button class="btn" v-if="change" @click="save">
            Save
        </button>
        <button class="btn delete" @click="deleteInstance">
            Delete
        </button>
    </div>
  </div>
</template>

<script>
import { members } from "../firebase"
import { setDoc, doc, deleteDoc } from "firebase/firestore"
import { getStorage, ref, getDownloadURL, uploadBytes, deleteObject } from "firebase/storage"

export default {
    name: "MemberTile",
    props: {
        member: Object,
        memberID: String,
        subteams: Array
    },
    data() {
        return {
            info: {},
            imageURL: "",
            change: false,
            newImage: false
        }
    },
    created() {
        this.info = this.$props.member
        
        if(this.info.image.length > 0)
            getDownloadURL(ref(getStorage(), this.info.image)).then((url) => this.imageURL = url)
    },
    watch: {
        info: {
            handler(newInfo, oldInfo)
            {
                if(Object.keys(oldInfo).length > 0)
                    this.change = true;
            },
            deep: true
        }
    },
    methods: {
        previewImage: function()
        {  
            const file = this.$refs.files.files[0]
            if(file)
            {
                this.imageURL = URL.createObjectURL(file)
                this.change = true;
                this.newImage = true;
            }
        },
        save: async function()
        {
            let imageURL = this.imageURL

            if(this.newImage)
            {
                let type = this.$refs.files.files[0].name.split(".").pop()
                imageURL = `${this.info.first_name}${this.info.last_name}.${type}`
                uploadBytes(ref(getStorage(), imageURL), this.$refs.files.files[0])
            }
            this.info.image = imageURL

            await setDoc(doc(members, this.$props.memberID), this.info ).then(() => {
                this.change = false
                this.newImage = false
            })
        },
        deleteInstance: async function()
        {
            if(confirm(`Are you sure you want to delete ${this.info.first_name}?`))
            {
                if (this.info.image.length > 0)
                    deleteObject(ref(getStorage(), this.$props.member.image))
                
                await deleteDoc(doc(members, this.$props.memberID)).then(() => {
                    this.$el.remove()
                })

            }
        }
    }
}
</script>

<style lang="scss" scoped>
.member-tile
{
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    box-sizing: border-box;
    padding: 2em;
    gap: 0.5em;

    .image-wrap{
        width: 7em;
        height: 7em;
        border-radius: 10em;
        overflow: hidden;
        position: relative;

        img
        {
            position: absolute;
            width: 100%;
            background-size: auto;
            background-position: center;
            z-index: 1;
            left: 0;
            top: 0;
        }

        input[type=file]
        {
            position: relative;
            width: 100%;
            height: 100%;
            z-index: 2;
            opacity: 0;
            &:hover{
                cursor: pointer;
            }
        }
    }

    .info-wrap
    {
        display: flex;
        flex-flow: column nowrap;
        justify-content: center;
        align-items: center;
    }

    .btn.delete
    {
        background-color: red;
    }
}
</style>